.post {
  display: grid;
  grid-template-columns: 100%;
  width: 100%;
  border: 0 solid var(--border-color-light);




  .postBody {
    order: 1;
    border-radius: 0;
    box-sizing: border-box;
    display: grid;
    grid-template-columns: 100%;
    width: 100%;
    overflow: hidden;
  }
}

